.logo {
    margin: 0 auto;
    max-width: 600px;
    padding-top: 86px;
    padding-bottom: 16px;
    overflow: hidden;
    
    img {
        width: 100%;
    }
}


@media screen and (max-width: 620px) {
    .logo {
        padding-top: 92px;
        padding: 92px 24px 32px 24px;
    }
}